import React, { Component } from 'react'
import {Button,Panel, Form,FormGroup, FormControl, ControlLabel} from 'react-bootstrap'

export default class MercadoPago extends Component {
    constructor(props){
        super(props)
        this.state={
            DNI:"",
            nTarjeta:"",
            Mes:"",
            Año:"",
            Codigo:"",
            error: null,
            mensaje: null
        }
        this.MercadoPago = this.MercadoPago.bind(this)
        this.aMarcos = this.aMarcos.bind(this)
    }
    handleChange = event =>{
        this.setState({
            [event.target.id]: event.target.value
        });
    }
    handleSubmit = event =>{
        event.preventDefault();    
    }
    async MercadoPago(){
        const response = await fetch("https://api.mercadopago.com/v1/card_tokens?public_key=TEST-4ee01e7f-08c9-42ea-a6e2-885bf99abd56",{
            method: 'POST',
            headers: {'Content-Type': 'application/json'},
            body: JSON.stringify({
                "cardholder":{
                    "name":"APRO_123",
                    "identification":{
                        "type": "DNI",
                        "number": this.state.DNI
                    }
                },
                "card_number": this.state.nTarjeta,
                "expiration_month": this.state.Mes,
                "expiration_year": this.state.Año,
                "security_code": this.state.Codigo
            
            })
        })
        console.log(response)
        const data = await response.json();
        sessionStorage.setItem('ID', data.id)
        await this.aMarcos()
        console.log(data)
        if(response.status >299){
            console.log(data.message)
        }
    }
    async aMarcos(){
        const response = await fetch("https://miapp-taller6.herokuapp.com/payment/pay",{
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Authorization' : 'bearer ' + localStorage.getItem('session'),
            },
            body: JSON.stringify({
                "amount": 10,
                "mp_payment_method_id": "master",
                "dni" : this.state.DNI,
                "mp_card_token": "" + sessionStorage.getItem('ID')
        
            })
        })
        console.log(response)
        const data = await response.json()
        console.log(data)
        if(response.status >= 200 && response.status < 299){
            this.setState({mensaje: "Pago realizado con exito. Número de transacción: " + data.transaction_id,
            error:null})
            sessionStorage.clear();
        }else{
            this.setState({error: data.message,mensaje:null})
        }
        
    }
    render() {
        return (
            <div onSubmit={this.handleSubmit}>
                <Panel>
                    <Panel.Heading><h1>Pagar</h1></Panel.Heading>
                    <Panel.Body>
                        <Form>
                            <FormGroup controlId="DNI">
                                <ControlLabel>DNI</ControlLabel>
                                <FormControl 
                                autoFocus
                                type="text"
                                value={this.state.DNI}
                                onChange={this.handleChange}/>
                            </FormGroup>
                            <FormGroup controlId="nTarjeta">
                                <ControlLabel>Numero de la tarjeta</ControlLabel>
                                <FormControl
                                autoFocus
                                type="text"
                                value={this.state.nTarjeta}
                                onChange={this.handleChange}/>
                            </FormGroup>
                            <FormGroup controlId="Mes" className="col-md-2">
                                <ControlLabel>Mes de expiración</ControlLabel>
                                <FormControl
                                autoFocus
                                type="text"
                                value={this.state.Mes}
                                onChange={this.handleChange}/>
                            </FormGroup>
                            <FormGroup controlId="Año" className="col-md-2">
                                <ControlLabel>Año de expiración</ControlLabel>
                                <FormControl
                                autoFocus
                                type="text"
                                value={this.state.Año}
                                onChange={this.handleChange}/>
                            </FormGroup>
                            <FormGroup controlId="Codigo" className="col-md-4">
                                <ControlLabel>Codigo de seguridad</ControlLabel>
                                <FormControl
                                autoFocus
                                type="text"
                                value={this.state.Codigo}
                                onChange={this.handleChange}/>
                            </FormGroup>
                        </Form>
                    </Panel.Body>
                    <Panel.Footer>
                        <Button onClick={this.MercadoPago} className="btn-success">Pagar</Button>
                        <Button onClick={this.aMarcos} className="btn-danger">a Marcos</Button>
                        <hr></hr>
                        <div style={{color: "#FF0000"}}>{this.state.error}</div>
                        <div style={{color: "#2E8B57"}}>{this.state.mensaje}</div>
                    </Panel.Footer>
                </Panel>
            </div>
        )
    }
}
