import React, { Component } from 'react'
import { Link } from "react-router-dom";
import { Nav, Navbar, NavItem } from "react-bootstrap";
import {LinkContainer} from "react-router-bootstrap";

export default class NavSession extends Component {
  constructor(props){
    super(props)
    this.state={
      isLoading: false,
      renderizado: null,
      renderizado1: null,
      renderizado2: null
    }
  }
  handleSubmit = async event =>{
    event.preventDefault();
    this.setState({isLoading: true})    
}
  logout(){
        localStorage.clear();
      }
      navRender() {
        let buttonRegistro,buttonLogin,btnBuscar,renderizado;
        if(localStorage){
          if (localStorage.getItem('session')) {
          buttonRegistro = <NavItem>Carrito</NavItem>
          btnBuscar = <NavItem>Productos</NavItem>
          buttonLogin = <NavItem onClick={this.logout}>Salir</NavItem>
          renderizado=
          <Nav pullRight>
            <LinkContainer to="Productos">
              {btnBuscar}
            </LinkContainer>
            <LinkContainer to="Carrito">
              {buttonRegistro}
            </LinkContainer>
            <LinkContainer to="Login">
              {buttonLogin}
            </LinkContainer>
          </Nav>
        }else if(localStorage.getItem('sessionAdmin')){
          buttonRegistro = <NavItem>CargarLibros</NavItem>
          btnBuscar = <NavItem>Usuarios</NavItem>
          buttonLogin = <NavItem onClick={this.logout}>Salir</NavItem>
          renderizado =
          <Nav pullRight>
            <LinkContainer to="BuscarUsuarios">
              {btnBuscar}
            </LinkContainer>
            <LinkContainer to="CargarLibros">
              {buttonRegistro}
            </LinkContainer>
            <LinkContainer to="Login">
              {buttonLogin}
            </LinkContainer>
          </Nav>
        }
        else{
          buttonRegistro = <NavItem>Registrarse</NavItem>
          buttonLogin = <NavItem>Ingresar</NavItem>;
          renderizado =
          <Nav pullRight>
            <LinkContainer to="Registrarse">
              {buttonRegistro}
            </LinkContainer>
            <LinkContainer to="Login">
              {buttonLogin}
            </LinkContainer>
          </Nav>
        }
      }      
        return (
            <div onSubmit={this.handleSubmit}>
            <Navbar fluid collapseOnSelect>
              <Navbar.Header>
                <Navbar.Brand>
                  <Link to="/" id="link">MercadoLibros</Link>
                </Navbar.Brand>
                <Navbar.Toggle />
              </Navbar.Header>
              <Navbar.Collapse>
                    {renderizado}
              </Navbar.Collapse>
            </Navbar>
            </div>
        )
    }
    render(){
      return(
        <div>
          {this.navRender()}
        </div>
      )
    }
}

